import {
  Autocomplete,
  Button,
  Checkbox,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Grid,
  IconButton,
  Snackbar,
  Tab,
  Tabs,
  TextField,
  Typography,
  Tooltip
} from "@mui/material";
import "./App.scss";
import AscendoIcon from "./assets/AscendoIcon";
import UpvoteIcon from "./assets/UpvoteIcon";
import DownvoteIcon from "./assets/DownvoteIcon";
import ViewsIcon from "./assets/ViewsIcon";
import ToptechnicianIcon from "./assets/ToptechnicianIcon";
import SuperexpertIcon from "./assets/SuperexpertIcon";
import LogoutIcon from "./assets/LogoutIcon";
import GenAiIcon from "./assets/GenAiIcon";
import CheckBoxOutlineBlankIcon from "@mui/icons-material/CheckBoxOutlineBlank";
import CheckBoxIcon from "@mui/icons-material/CheckBox";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { ShellSdk, SHELL_EVENTS } from "fsm-shell";

import {
  authorizeUser,
  getActivityDetails,
  getAllBU,
  getAllPG,
  getGenAiSolution,
  getPredictionResults,
  getSubDatasource,
  getSummarize,
  getUserInfo,
  toggleDocumentVote,
  updateAuthorizationError,
  updateAuthorized,
  updateDocumentId,
  updatePredictionDetails,
  updateQuery,
  updateSelectAll,
  updateSelectedBU,
  updateSelectedDS,
  updateSelectedPG,
  updateTabValue,
  updateWriteback,
} from "./redux/slice/zendeskSlice";
import validator from "validator";
const icon = <CheckBoxOutlineBlankIcon fontSize="small" />;
const checkedIcon = <CheckBoxIcon fontSize="small" />;

function App() {
  console.log("new")
  const [activityId,setActivityId]=useState()
  
  if (!ShellSdk.isInsideShell()) {
    // updateUI('Unable to reach shell eventAPI');
    console.log("Unable to reach shell eventAPI");
  } else {
    // Initialise ShellSDK to connect with parent shell library
    console.log("called shell");

    const shellSdk = ShellSdk.init(window.parent, "*");

    // Initialise the extension by requesting the fsm context
    shellSdk.emit(SHELL_EVENTS.Version1.REQUIRE_CONTEXT, {
      clientIdentifier: "service-contract",
      auth: {
        response_type: "token", // request a user token within the context
      },
    });
    shellSdk.on(SHELL_EVENTS.Version1.REQUIRE_CONTEXT, (event) => {

      const {
        // extract required context from event content
        cloudHost,
        account,
        company,
        user,
        viewState,
        // extract authentication data from event content
      } = JSON.parse(event);
      console.log("after emitted",JSON.parse(event));
      setActivityId(viewState?.selectedActivityId)


      // Add a listener expecting activityID
      

      });
    
  }

  const [query, setQuery] = useState();
  const [auth, setAuth] = useState(false);
  const {
    bu_details,
    selected_bu,
    user_details,
    pg_details,
    selected_pg,
    ds_details,
    selected_ds,
    zendesk_error,
    solution_isLoading,
    prediction_details,
    summarize_details,
    solution_loading,
    document_id,
    solution_details,
    tab_value,
    select_all,
    search_query,
    authorization_error,
    authorized,
    activity_details,
    is_summarizing
  } = useSelector((state) => state.zendeskReducer);
  const [customer, setCustomer] = useState();
  const [username, setUsername] = useState();
  const [email, setEmail] = useState();
  const [isValidEmail, setIsValidEmail] = useState(true);
  const [isValidCustomer, setIsValidCustomer] = useState(true);
  const [isValidPassword, setIsValidPassword] = useState(true);
  const [customerHelper, setCustomerHelper] = useState();
  const [emailHelper, setEmailHelper] = useState();
  const [passHelper, setPassHelper] = useState();

  const [password, setPassword] = useState();
  const [openSnack, setOpenSnack] = useState(false);

  const handleEmailChange = (e) => {
    const newEmail = e.target.value;
    const newIsValid = validator.isEmail(newEmail);
    setUsername(e.target.value);
    setEmail(newEmail);
    setIsValidEmail(newIsValid);
    setEmailHelper(newIsValid ? null : "Enter a valid email address");
  };

  const handleEmailFocus = (e) => {
    const newEmail = e.target.value;
    const newIsValid = validator.isEmail(newEmail);
    setUsername(e.target.value);
    setEmail(newEmail);
    setIsValidEmail(newIsValid);
  };

  const handleEmailBlur = () => {
    setEmailHelper(isValidEmail ? null : "Enter a valid email address");
  };

  const handleCustomerFocus = (e) => {
    const customer = e.target.value;
    setIsValidCustomer(customer);
  };
  const handleCustomerBlur = () => {
    setCustomerHelper(
      isValidCustomer ? null : "Enter a valid organization name"
    );
  };

  const handleCustomerChange = (e) => {
    const customer = e.target.value;
    setIsValidCustomer(customer);
    setCustomer(e.target.value);
    setCustomerHelper(customer ? null : "Enter a valid organization name");
  };

  const handlePasswordFocus = (e) => {
    const password = e.target.value;
    setIsValidPassword(password);
  };

  const handlePasswordBlur = () => {
    setPassHelper(isValidPassword ? null : "Enter a valid password");
  };

  const handlePasswordChange = (e) => {
    const password = e.target.value;
    setIsValidPassword(password);
    setPassword(e.target.value);
    setPassHelper(password ? null : "Enter a valid password");
  };

  const handleSnackBarClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpenSnack(false);
  };
  const action = (
    <Button
      sx={{ color: "#F9BE02" }}
      size="small"
      onClick={handleSnackBarClose}
    >
      Okay
    </Button>
  );
  const [open, setOpen] = useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

  const handleLogout = () => {
    handleClose();
    localStorage.clear()
    dispatch(updateTabValue(1))
    dispatch(updateAuthorizationError({ detail: "Successfully logged out" }));

    dispatch(updateAuthorized(false));
  };

  const handleLogin = () => {
    let details = {
      customer: customer,
      username: username,
      password: password,
    };
    if (customer && username && password) dispatch(authorizeUser(details));
    else {
      setIsValidCustomer(customer);
      setIsValidEmail(email);
      setIsValidPassword(password);
      setCustomerHelper(customer ? null : "Enter a valid organization name");

      setEmailHelper(email ? null : "Enter a valid email address");
      setPassHelper(password ? null : "Enter a valid password");
    }

    setOpenSnack(false);
  };
  useEffect(() => {
    if (authorization_error) {
      setOpenSnack(true);
    }
  }, [authorization_error]);
  useEffect(() => {
    if (localStorage.getItem("auth")) {
      setAuth(true);
      dispatch(getUserInfo());
    } else if (authorized === true) {
      dispatch(getUserInfo());

      setAuth(true);
    } else if (authorized === false) {
      localStorage.clear();

      setAuth(false);
    }
  }, [authorized]);



  const dispatch = useDispatch();

  const handleSelectedDS = (option) => {
    if (option.uuid === "home28082001") {
      if (select_all) {
        dispatch(updateSelectedDS([]));
        dispatch(updateSelectAll(false));
      } else {
        dispatch(updateSelectedDS(ds_details));
        dispatch(updateSelectAll(true));
      }
    } else if (selected_ds.includes(option)) {
      dispatch(
        updateSelectedDS(
          selected_ds.filter((item) => item.uuid !== option.uuid)
        )
      );
    } else {
      dispatch(updateSelectedDS([...selected_ds, option]));
    }
  };
  const handleGenAI = (documentId) => {
    dispatch(updateDocumentId(documentId));
    dispatch(updateTabValue(2));
  };

  const handleToggleVote = (
    document_id,
    sub_datasource_uuid,
    vote_direction
  ) => {
    let vote_details = {
      business_unit_uuid: selected_bu.uuid,
      sub_datasource_uuid: sub_datasource_uuid,
      document_id: document_id,
      query: "",
      product_group: selected_pg.name,
      vote_direction: vote_direction,
    };
    const updatedPredictionResults = {
      data: prediction_details?.data?.map((item) =>
        item._id === document_id
          ? {
              ...item,
              user_vote_direction:
                vote_direction === item.user_vote_direction
                  ? null
                  : vote_direction,
              up_vote_count:
                vote_direction === "UP"
                  ? item.user_vote_direction === "UP"
                    ? item.up_vote_count - 1
                    : item.up_vote_count + 1
                  : vote_direction === "DOWN"
                  ? item.user_vote_direction === "UP"
                    ? item.up_vote_count - 1
                    : item.up_vote_count
                  : item.up_vote_count,
              down_vote_count:
                vote_direction === "DOWN"
                  ? item.user_vote_direction === "DOWN"
                    ? item.down_vote_count - 1
                    : item.down_vote_count + 1
                  : vote_direction === "UP"
                  ? item.user_vote_direction === "DOWN"
                    ? item.down_vote_count - 1
                    : item.down_vote_count
                  : item.down_vote_count,
            }
          : item
      ),
    };
    dispatch(updatePredictionDetails(updatedPredictionResults));
    dispatch(toggleDocumentVote(vote_details));
  };
  const handlePredict = () => {
   
    dispatch(updateQuery(query));
    dispatch(updateTabValue(1));
  };
  const handleClear = () => {
    dispatch(updateSelectedDS([]));
  };

  useEffect(() => {
    if (localStorage.getItem("ci")) dispatch(getUserInfo());
  }, []);
  useEffect(()=>{
    if(activityId&&(localStorage.getItem("auth")||authorized)){
  dispatch(getActivityDetails(activityId))
    }
  },[activityId])
  useEffect(()=>{
if(activity_details){
  setQuery(activity_details?.subject)
  dispatch(updateQuery(activity_details?.subject))


 
}
  },[activity_details])
  useEffect(()=>{
    if(search_query&&selected_bu&&selected_pg&&selected_ds.length>0){
    let data = {
      business_unit_uuid: selected_bu.uuid,
      query: search_query,
      product_group: selected_pg.name,
      sub_datasource_uuids: selected_ds.map((item) => item.uuid),
    };

    dispatch(getPredictionResults(data));}
  },[search_query,selected_bu,selected_ds,selected_pg])

  useEffect(() => {
    if (user_details) {
      dispatch(getAllBU());
    }
  }, [user_details]);

  useEffect(() => {
    if (document_id) {
      let data = {
        business_unit_uuid: selected_bu.uuid,
        sub_datasource_uuids: selected_ds.map((item) => item.uuid),
        document_id: document_id,
      };
      dispatch(getGenAiSolution(data));
    }
  }, [document_id]);

  useEffect(() => {
    if (selected_bu) {
      dispatch(getAllPG(selected_bu.uuid));
      dispatch(getSubDatasource(selected_bu.uuid));
    }
  }, [selected_bu]);
  return (
    <div className="App">
      <Grid
        sx={{
          height: "54px",
          bgcolor: "#071942",
          display: "flex",
          alignItems: "center",
          p: "16px",
        }}
      >
        <AscendoIcon />
      </Grid>

      <Grid sx={{ p: "16px" }}>
        {!auth && (
          <Grid
            display="flex"
            flexDirection="column"
            gap="8px"
            pt="6px"
            overflow="hidden"
          >
            {/* <Box sx={{backgroundColor:"white",borderRadius:"6px",padding:"20px"}} display="flex" flexDirection="column" justifyContent="center" alignItems="center" gap="8px" > */}
            {/* <AscendoIcon/>
        <Typography>
            Please login to your account to continue
        </Typography> */}
            <Grid display="flex" flexDirection="column" gap="12px">
              <TextField
                fullWidth
                required
                label="Organization Name"
                size="small"
                onFocus={handleCustomerFocus}
                onBlur={handleCustomerBlur}
                helperText={customerHelper}
                error={!isValidCustomer}
                onChange={handleCustomerChange}
              />
              <TextField
                fullWidth
                required
                label="Email"
                size="small"
                type="email"
                onFocus={handleEmailFocus}
                onBlur={handleEmailBlur}
                helperText={emailHelper}
                error={!isValidEmail}
                onChange={handleEmailChange}
              />
              <TextField
                fullWidth
                required
                label="Password"
                size="small"
                type="password"
                onFocus={handlePasswordFocus}
                onBlur={handlePasswordBlur}
                helperText={passHelper}
                error={!isValidPassword}
                onChange={handlePasswordChange}
              />
            </Grid>
            <Grid
              display="flex"
              flexDirection="column"
              gap="12px"
              justifyContent="end"
              alignItems="end"
            >
              <Button
                fullWidth
                size="small"
                sx={{
                  backgroundColor: "#F9BE02",
                  color: "#071942",
                  mt: "10px",
                  maxWidth: "100px",
                }}
                onClick={handleLogin}
              >
                Login
              </Button>
              

            </Grid>

            {/* </Box> */}
          </Grid>
        )}
        {auth && (
          <Grid>
            <Grid mb="16px">
              <Grid sx={{ display: "flex", gap: "16px" }}>
                <TextField
                  sx={{ flex: "1 1 0" }}
                  size="small"
                  value={query}
                  onChange={(e) => setQuery(e.target.value)}
                  placeholder="Enter your query here..."
                  onKeyDown={(e) => {
                    if (e.key === "Enter") handlePredict();
                  }}
                />
                <Grid
                  sx={{
                    height: "40px",
                    backgroundColor: "#F9BE02",
                    px: "12px",
                    py: "7px",
                    borderRadius: "6px",
                    cursor: "pointer",
                    ":hover": { backgroundColor: "rgba(249, 190, 2, 0.88)" },
                  }}
                  onClick={handlePredict}
                >
                  <img src="https://storage.googleapis.com/exteneral_logo/sap/Predict.svg"></img>
                </Grid>
              </Grid>

              <Grid
                sx={{
                  display: "flex",
                  justifyContent: "space-between",
                  pt: "16px",
                }}
              >
                <Autocomplete
                  fullWidth
                  id="checkboxes-tags-demo"
                  disableClearable
                  options={bu_details}
                  value={selected_bu}
                  size="small"
                  onChange={(event, newValue) => {
                    dispatch(updateSelectedBU(newValue));
                  }}
                  disableCloseOnSelect
                  getOptionLabel={(option) => option.name}
                  isOptionEqualToValue={(option, value) =>
                    option?.uuid === value?.uuid
                  }
                  renderInput={(params) => (
                    <TextField {...params} label="Business Unit" />
                  )}
                />
                <Autocomplete
                  fullWidth
                  id="checkboxes-tags-demo"
                  disableClearable
                  options={pg_details}
                  value={selected_pg}
                  size="small"
                  onChange={(event, newValue) => {
                    dispatch(updateSelectedPG(newValue));
                  }}
                  disableCloseOnSelect
                  getOptionLabel={(option) => option.name}
                  isOptionEqualToValue={(option, value) =>
                    option.uuid === value.uuid
                  }
                  style={{ marginLeft: "16px" }}
                  renderInput={(params) => (
                    <TextField {...params} label="Product Group" />
                  )}
                />

                <Autocomplete
                  fullWidth
                  multiple
                  id="checkboxes-tags-demo"
                  options={[
                    { name: "Select All", uuid: "home28082001" },
                    ...ds_details,
                  ]}
                  value={selected_ds}
                  limitTags={1}
                  size="small"
                  disableCloseOnSelect
                  getOptionLabel={(option) => option.name}
                  isOptionEqualToValue={(option, value) =>
                    option.uuid === value.uuid
                  }
                  slotProps={{ clearIndicator: { onClick: handleClear } }}
                  ChipProps={{
                    style: {
                      backgroundColor: "rgba(26, 115, 232, 0.08)",
                      color: "#1A73E8",
                      maxWidth: "100px",

                      width: "65px",
                      minWidth: "50px",
                      fontSize: "12px",
                      height: "18px",
                      borderRadius: "4px",
                      marginBottom: "6px",
                      flexDirection: "row",
                    },
                  }}
                  sx={{
                    "& .MuiOutlinedInput-root.MuiInputBase-sizeSmall": {
                      height: "40px",
                      overflowY: "hidden",
                    },
                    "& .MuiAutocomplete-tag.MuiAutocomplete-tagSizeSmall": {
                      fontSize: "12px",
                      color: "#1A73E8",
                    },
                  }}
                  renderOption={(props, option, { selected }) => {
                    const isSelected =
                      option.uuid !== "home28082001"
                        ? selected_ds?.includes(option)
                        : selected_ds.length === ds_details.length;
                    return (
                      <li
                        style={{ marginLeft: "-12px" }}
                        {...props}
                        onClick={() => handleSelectedDS(option)}
                      >
                        <Checkbox
                          icon={icon}
                          checkedIcon={checkedIcon}
                          checked={isSelected}
                        />
                        {option.name}
                      </li>
                    );
                  }}
                  style={{ marginLeft: "16px" }}
                  renderInput={(params) => (
                    <TextField {...params} label="Datasources" />
                  )}
                />
              </Grid>
              <Grid sx={{ pt: "16px", borderBottom: "0.5px solid grey" }}>
                <Tabs
                  value={tab_value}
                  onChange={(e, newValue) => dispatch(updateTabValue(newValue))}
                >
                  <Tab value={1} label="SOLUTION" />
                  <Tab value={2} label="GENERATE AI" />
                  {/* <Tab value={0} label="SUMMARIZE" /> */}

                </Tabs>
              </Grid>
            </Grid>
            <Grid>
              {tab_value === 0 && (
                <Grid>
                  <div className="summarize-wrap">
                    {!summarize_details ?!is_summarizing?(<div>
                          <Button  sx={{color:"#071942",backgroundColor:"#F9BE02",textTransform:"none",fontSize:"12px",paddingX:"14px"}} onClick={()=>dispatch(getSummarize(activityId))}>
                            Summarize this Activity
                          </Button>
                          </div>): (
                      <Typography fontSize="13px">Loading...</Typography>
                    ) : (
                      <>
                        {/* <ul>
                          <b>Intent</b>
                          <li>
                            {summarize_details?.intent ??
                              "No user interaction found"}
                          </li>
                        </ul>
                        <ul>
                          <b>Sentiment</b>
                          <li>
                            {summarize_details?.sentiment ??
                              "No user interaction found"}
                          </li>
                        </ul> */}
                        <ul>
                          <b>Summary</b>
                          <li
                            style={{ whiteSpace: "pre-line" }}
                            dangerouslySetInnerHTML={{
                              __html:
                                summarize_details?.summary ??
                                "No interaction found",
                            }}
                          ></li>
                        </ul>
                        <div className="btn-wrap">
                          <Button  sx={{color:"#071942",backgroundColor:"#F9BE02",textTransform:"none",fontSize:"12px",paddingX:"14px"}} onClick={()=>dispatch(updateWriteback({activity_id:activityId,summary:summarize_details?.summary}))}>
                            Add Summary To Activity
                          </Button>
                          </div>
                      </>
                    )}
                  </div>
                </Grid>
              )}
              {tab_value === 1 && (
                <Grid>
                  {solution_isLoading ? (
                    <Typography fontSize="13px">Loading...</Typography>
                  ) : selected_ds.length > 0 ? (
                    prediction_details?.data?.length > 0 ? (
                      <div className="solution-wrap">
                        <div className="band search" id="searchPage">
                          <ul className="cards">
                            <li className="question-desc">
                              {prediction_details?.data?.map((item) => (
                                <div className="card">
                                  <div className="card-content">
                                    <div className="cards-link">
                                      <div className="card-text">
                                        <span className="colored-dropdown"></span>{" "}
                                        <b title={item.Title}>{item.Title}</b>
                                        <span
                                          className="count"
                                          style={{
                                            backgroundColor:
                                              item.probability > 70
                                                ? "#32C49E"
                                                : item.probability > 30
                                                ? "#EF9E42"
                                                : "E25757",
                                          }}
                                        >
                                          {item.probability}
                                        </span>
                                        <p className="desc-id">
                                          <span className="alink">
                                            #{item["Unique Number"]}
                                          </span>
                                          {/* <span className="update-date">
                      Updated at 10 Jun, 2023
                    </span> */}
                                        </p>
                                        <p className="desc-answer">
                                          {item.Description}
                                        </p>
                                      </div>
                                    </div>
                                    <div className="icon-list">
                                      <ul>
                                      <Tooltip title="Upvote">

                                        <li>
                                          <span>
                                            <IconButton
                                              className="updown-icons"
                                              onClick={() =>
                                                handleToggleVote(
                                                  item._id,
                                                  item.sub_datasource_uuid,
                                                  "UP"
                                                )
                                              }
                                            >
                                              <UpvoteIcon
                                                fill={
                                                  item.user_vote_direction ===
                                                  "UP"
                                                    ? "#071942"
                                                    : "none"
                                                }
                                              />
                                            </IconButton>
                                            {item.up_vote_count}
                                          </span>
                                        </li>
                                        </Tooltip>
                                        <Tooltip title="Downvote">

                                        <li>
                                          <span>
                                            <IconButton
                                              className="updown-icons"
                                              onClick={() =>
                                                handleToggleVote(
                                                  item._id,
                                                  item.sub_datasource_uuid,
                                                  "DOWN"
                                                )
                                              }
                                            >
                                              <DownvoteIcon
                                                fill={
                                                  item.user_vote_direction ===
                                                  "DOWN"
                                                    ? "#071942"
                                                    : "none"
                                                }
                                              />
                                            </IconButton>
                                            {item.down_vote_count}
                                          </span>
                                        </li>
                                        </Tooltip>
                                        <Tooltip title="Views">

                                        <li>
                                          <span className="links">
                                            <ViewsIcon />
                                            {item.view_data.count}
                                          </span>
                                        </li>
                                        </Tooltip>
                                        <Tooltip title="Top Technician Vote Count">

                                        <li>
                                          <span className="links">
                                            <ToptechnicianIcon />
                                            {item.expert_up_vote_count}
                                          </span>
                                        </li>
                                        </Tooltip>
                                        <Tooltip title="Super Expert Vote Count">
                                        <li>
                                          <span className="links">
                                            <SuperexpertIcon />
                                            {item.super_expert_up_vote_count}
                                          </span>
                                        </li>
                                        </Tooltip>
                                        {/* <Tooltip title="Generate Solution">
                                        <li>
                                          <span
                                            className="links generatebtn"
                                            style={{ cursor: "pointer" }}
                                            onClick={() =>
                                              handleGenAI(item._id)
                                            }
                                          >
                                            <GenAiIcon />
                                          </span>
                                        </li>
                                        </Tooltip> */}
                                      </ul>
                                    </div>
                                  </div>
                                </div>
                              ))}
                            </li>
                          </ul>
                        </div>
                      </div>
                    ) : (
                      <Typography fontSize="13px">No results found</Typography>
                    )
                  ) : (
                    <Typography fontSize="13px">
                      Please select atleast one datasource to view the results
                    </Typography>
                  )}
                </Grid>
              )}
              {tab_value === 2 && (
                <Grid>
                  {solution_loading ? (
                    <Typography fontSize="13px">
                      {" "}
                      {document_id
                        ? "Loading..."
                        : "Select a document from the solution tab to see Generate AI solution"}
                    </Typography>
                  ) : (
                  <div className="genAi-wrap">
                    {solution_details?.summary?.length > 0 ? (
                      <Grid>
                        <Grid>
                          <Typography
                            fontSize="13px"
                            whiteSpace="pre-line"
                            dangerouslySetInnerHTML={{
                              __html: solution_details?.summary,
                            }}
                          />
                        </Grid>
                        
                      </Grid>
                    ) : (
                      <Typography fontSize="13px" whiteSpace="pre-line">
                        No results found
                      </Typography>
                    )}
                  </div>
                  )} 
                </Grid>
              )}
            </Grid>
            <Dialog
              open={open}
              onClose={handleClose}
              aria-labelledby="alert-dialog-title"
              aria-describedby="alert-dialog-description"
            >
              <DialogTitle id="alert-dialog-title">
                {"Are you sure?"}
              </DialogTitle>
              <DialogContent>
                <DialogContentText id="alert-dialog-description">
                  Are you sure you want to logout?
                </DialogContentText>
              </DialogContent>
              <DialogActions sx={{ pr: 3, pb: 2.5 }}>
                <Button
                  width="75px"
                  sx={{ color: "rgba(7, 25, 66)", textTransform: "none" }}
                  onClick={handleClose}
                >
                  Cancel
                </Button>
                <Button
                  width="75px"
                  sx={{
                    color: "rgba(7, 25, 66)",
                    textTransform: "none",
                    backgroundColor: "#F9BE02",
                  }}
                  onClick={handleLogout}
                  autoFocus
                >
                  Logout
                </Button>
              </DialogActions>
            </Dialog>
            <div className="logout-wrap" title="Log out">
              <IconButton onClick={handleClickOpen}>
                <LogoutIcon />
              </IconButton>
            </div>
          </Grid>
        )}
      </Grid>
      <Snackbar
                anchorOrigin={{ vertical: "bottom", horizontal: "center" }}
                open={openSnack}
                message={authorization_error?.detail??authorization_error?.message}
                action={action}
                onClose={handleSnackBarClose}
                autoHideDuration={3500}
              />
    </div>
  );
}

export default App;
